<template>
  <div class>
    <div class="bg-white h-1" />
    <div class="flex">
      <div v-for="s in steps" :key="s" class="flex-1">
        <div
          v-if="step >= s"
          class="bg-green-500 -mt-1 h-1"
          :class="{ 'w-1/2': step == s, 'w-full': step < s }"
        />
        <div
          class="-mt-3 mx-auto rounded-full h-5 w-5"
          :class="{ 'bg-green-500': s <= step, 'bg-white': step != s }"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['steps', 'step'],
};
</script>
